import React from 'react'
import Text from "./types/text"
import "./index.css"
import Dropdown from './types/dropdown'
import Checkbox from './types/checkbox'

const Input = React.memo(props => {
    const { type, min, max, onEditComplete, onFocusChange, post, options, def, key, label } = props
    switch(type){
        case 'select':
            return <Dropdown
                type={type}
                onEditComplete={onEditComplete}
                onFocusChange={onFocusChange}
                options={options}
                def={def}
                key={key}
            />
        case 'bool':
            return <Dropdown
                type={type}
                onEditComplete={onEditComplete}
                onFocusChange={onFocusChange}
                options={(options || ['Yes', 'No']).slice(0, 2).map((o, i) => { return { name: o, value: i%2==0 } })}
                def={def}
                key={key}
            />
        case 'checkbox':
            return <Checkbox
                onEditComplete={onEditComplete}
                onFocusChange={onFocusChange}
                def={def}
                key={key}
                label={label}
            />
        default:
            return <Text
                type={type}
                min={min}
                max={max}
                onEditComplete={onEditComplete}
                onFocusChange={onFocusChange}
                post={post}
                def={def}
                key={key}
            />
    }
})

export default Input

