import moment from "moment"
  
  const get=()=>{
    try {
      return JSON.parse(localStorage.getItem("save"))
    }catch{
      return null
    }
  }
  
  const set=(store)=>{
    try {
      localStorage.setItem("save", JSON.stringify(store, null, 0), { expires: moment().add(100, 'years').toDate()})
      return true
    }catch{
      return false
    }
  }
  
  const clear=()=>{
    try {
      localStorage.removeItem("save")
      return true
    }catch{
      return false
    }
  }
  
  const auth=()=>{

    const openRoutes = [
      '/', '/register'
    ]

    if(openRoutes.indexOf(window.location.pathname)!=-1){
      return
    }

    const store = localStorage.getItem('save')
    if(window.location.pathname == '/login'){
      if(store){
        return new URLSearchParams(window.location.search).get('redirect') || '/home'
      }        
    }else{
      if(!store){
        return `/login?redirect=${window.location.pathname}`
      }
    }
  }
  
  const user=()=>{
    try {
      const store = JSON.parse(localStorage.getItem("save"))
      return store.users[store.index]
    }catch{
      return null
    }
  }

  const users=()=>{
    try {
      const store = JSON.parse(localStorage.getItem("save"))
      return store.users
    }catch{
      return null
    }
  }  

  const store = {
    get, set, clear, auth, user, users
  }

  export default store