import "./index.css"
import DP from "../../components/dp"
import { useCallback, useEffect, useMemo, useRef, useState } from "react" 
import _ from "../../_"
import Form from "../../components/form"
import { useGlobal } from "../../context/global"
import PageLoader from "../../components/pageloader"
import { toast } from 'react-toastify'
import Icon from "../../components/icon"
import { useCropLoader, CropLoader } from "../../components/croploader"
import moment from "moment"

const Register = () => {

    const { api } = useGlobal()
    var [ formConfig, setFormConfig ] = useState()
    const minPhotoSize = 500
    const imageKey = 'volimage'
    const { select, crop, upload, setSelected, selected, cropped } = useCropLoader(minPhotoSize, localStorage.getItem(imageKey))

    var [ loaderText, setLoaderText ] = useState()

    useEffect(()=>{
        const curYear = parseInt(moment().format("YYYY"))
        const today = moment().format("YYYY-MM-DD")
        const getConfig = res =>
            [
                {
                    section: 'Contact Details',
                    fields: [
                        {
                            title: "Full Name",
                            name: "name",
                            mandatory: true,
                            post: v => v && v.toNameCase()
                        },
                        {
                            title: "Phone Number",
                            name: "phone",
                            mandatory: true,
                            type: 'tel',
                            post: v => isNaN(v)?v:v.toPhoneCase(),
                            test: v=> v && !_.phoneRegex.test(v)?'Invalid phone number': ''
                        },
                        {
                            title: "Do you have WhatsApp number?",
                            name: "hasWhatsApp",
                            mandatory: true,
                            type: 'bool',
                            empty: [false],
                        },
                        {
                            title: "WhatsApp number",
                            name: "whatsapp",
                            mandatory: true,
                            type: 'tel',
                            post: v => isNaN(v)?v:v.toPhoneCase(),
                            test: v=> v && !_.phoneRegex.test(v)?'Invalid phone number': '',
                            condition: values => values && values.hasWhatsApp
                        },
                        {
                            title: "Email ID",
                            name: "email",
                            type: 'email',
                            mandatory: values => values && !values.hasWhatsApp, 
                            test: v=> v && !_.emailRegex.test(v)?'Invalid Email ID': ''
                        },
                        {
                            title: "Address",
                            name: "address",
                            type: 'para',
                            mandatory: true
                        },
                        {
                            title: 'Have you taken initiation?',
                            name: 'hasInitiation',
                            type: 'bool',
                            options: ['Yes', 'No']
                        },
                        {
                            title: 'Your Initiation-name',
                            name: 'initiationName',
                            type: 'text',
                            mandatory: true,
                            post: v => v && v.toNameCase(),
                            condition: v => v.hasInitiation
                        }
                    ]
                },
                {
                    section: "Basic Details",
                    fields: [
                        {
                            title: "Date of Birth",
                            name: "dob",
                            mandatory: true,
                            type: 'date',
                            min: moment().subtract(100, 'years').format('YYYY-MM-DD'),
                            max: moment().format('YYYY-MM-DD'),
                            test: v => v && (v<=today && v>=today.replace(curYear+'', (curYear-100)+''))?'':`Year must between ${today.replace(curYear+'', (curYear-100)+'')} and ${today}`
                        },
                        {
                            title: "Gender",
                            name: "gender",
                            mandatory: true,
                            type: 'bool',
                            options: ['Male', 'Female'],
                            empty: [false],
                        },
                        {
                            title: "Marital Status",
                            name: "married",
                            mandatory: true,
                            type: 'bool',
                            empty: [false],
                            options: ['Married', 'Unmarried']
                        },
                        {
                            title: values => values && (values.gender?`Father's Name`:values.married?`Husband's Name`:`Father's Name`),
                            name: "relativeName",
                            mandatory: true,
                            post: v => v && v.toNameCase(),
                            condition: values => values && (values.gender!==undefined && values.married!=undefined)
                        },
                        {
                            title: values => values && (values.gender?`Father's Phone`:values.married?`Husband's Phone`:`Father's Phone`),
                            name: "relativePhone",
                            type: 'tel',
                            post: v => isNaN(v)?v:v.toPhoneCase(),
                            test: v=> v && !_.phoneRegex.test(v)?'Invalid phone number': '',
                            condition: values => values && (values.gender!==undefined && values.married!=undefined)
                        },
                        {
                            title: values => values && (values.gender?`Father's Profession`:values.married?`Husband's Profession`:`Father's Profession`),
                            name: "relativeProfession",
                            mandatory: true,
                            condition: values => values && (values.gender!==undefined && values.married!=undefined)
                        },
                        {
                            title: "Native",
                            name: "native",
                            mandatory: true,
                            type: 'text'
                        },              
                        {
                            title: "Hobbies (if any)",
                            name: "hobbies",
                            type: 'para'
                        },
                        {
                            title: "Skills (if any)",
                            name: "skills",
                            type: 'para'
                        },
                        {
                            title: "Achievements (if any)",
                            name: "achievements",
                            type: 'para'
                        }
                    ]
                },
                {
                    section:"Occupational Details",
                    fields: [
                        {
                            title: "Occupational Status",
                            name: "occupationalStatus",
                            type: 'select',
                            mandatory: true,
                            options: ['Student', 'Working', 'Seeking Job', 'Home-Maker', 'Retired']
                        },

                        {
                            title: "Institution",
                            name: "institution",
                            mandatory: values => values && ['Student', 'Seeking Job'].indexOf(values.occupationalStatus)!=-1,
                            desc: values => values?`Mention name of the ${values.occupationalStatus=='Student'?"school/college":"institution"} where you ${values.occupationalStatus=='Student'?"are studying":"last studied in"}`:'',
                            post:  v => v && v.toTitleCase(),
                            condition: values => values && (['Student', 'Seeking Job', 'Home-Maker'].indexOf(values.occupationalStatus)!=-1)
                        },
                        {
                            title: "Course",
                            name: "course",
                            mandatory: values => values && ['Student', 'Seeking Job'].indexOf(values.occupationalStatus)!=-1,
                            post:  v => v && v.toTitleCase(),
                            condition: values => values && (['Student', 'Seeking Job', 'Home-Maker'].indexOf(values.occupationalStatus)!=-1)
                        },
                        {
                            title: values => values?(values.occupationalStatus=='Student'?"Year of Joining":"Year of Course Completion"):"Year",
                            name: "studyYear",
                            mandatory: values => values && ['Student', 'Seeking Job'].indexOf(values.occupationalStatus)!=-1,
                            desc: 'Mention the year in which you joined the above mentioned course',
                            type: 'number',
                            max: curYear,
                            min: curYear-80,
                            condition: values => values && (['Student', 'Seeking Job', 'Home-Maker'].indexOf(values.occupationalStatus)!=-1),
                            test: v => v && (v<=curYear && v>=curYear-80)?'':`Year must between ${curYear-80} and ${curYear}`
                        },

                        {
                            title: "Organization",
                            name: "organization",
                            mandatory: true,
                            desc: values => `Mention name of the company/business/organization you ${values.occupationalStatus=='Working'?'are working':'last worked'} for`,
                            post:  v => v && v.toTitleCase(),
                            condition: values => values && (values.occupationalStatus=='Working' || values.occupationalStatus=='Retired'),
                        },
                        {
                            title: "Designation",
                            name: "designation",
                            mandatory: true,
                            desc: 'Mention your title/role of your work',
                            post:  v => v && v.toTitleCase(),
                            condition: values => values && (values.occupationalStatus=='Working' || values.occupationalStatus=='Retired'),
                        },
                        {
                            title: "Highest Qualification",
                            name: "highestQualification",
                            mandatory: true,
                            desc: 'Mention your last educational qualification',
                            post:  v => v && v.toTitleCase(),
                            condition: values => values && (values.occupationalStatus=='Working' || values.occupationalStatus=='Retired')
                        }
                    ]
                },
                {
                    section: 'Your association with ISKCON',
                    fields: [
                        {
                            title: "When did you come in touch with ISKCON?",
                            name: "touch",
                            mandatory: true,
                            type: 'date',
                            desc: 'Mention month and year',
                            min: moment().subtract(100, 'years').format('YYYY-MM-DD'),
                            max: moment().format('YYYY-MM-DD'),
                            test: v => v && (v<=today && v>=today.replace(curYear+'', (curYear-100)+''))?'':`Year must between ${today.replace(curYear+'', (curYear-100)+'')} and ${today}`
                        },
                        {
                            title: "How many rounds of Hare Krishna Maha Mantra do you chant daily?",
                            name: "rounds",
                            mandatory: true,
                            type: 'select',
                            options: Array.from(Array(17).keys()),
                            empty: [0]
                        },
                        {
                            title: "Which of the below devotees are you in touch with?",
                            name: "preacher",
                            mandatory: true,
                            type: 'select',
                            options: res.data.map(d => {
                                return {
                                    name: d.name,
                                    value: d.id
                                }
                            }),
                            empty: [null]
                        }
                    ]
                }
            ]

        api.call('/get-preachers').then(res=>{
            setFormConfig(getConfig(res))
        }).catch(err=>{
            console.log(err)
            toast.error(`${err}`)
        })
    }, [])

    useEffect(()=>{
        cropped && localStorage.setItem(imageKey, cropped)
    }, [cropped])

    const submitForm = useCallback((formData, resetForm)=>{
        if(!cropped){
            toast.error('Upload your photo before submitting the form.')
            return
        }

        if(formData && cropped){

            setLoaderText('Submitting form...')

            const encode = (value) => (value ? btoa(value) : null)
            const encoded = {
                ...formData,
                approval: 'ops-pending',
                date: moment().format('YYYY-MM-DD'),
          
                address: encode(formData.address),
                relativeProfession: encode(formData.relativeProfession),
                native: encode(formData.native),
                hobbies: encode(formData.hobbies),
                skills: encode(formData.skills),
                achievements: encode(formData.achievements),
                institution: encode(formData.institution),
                course: encode(formData.course),
                organization: encode(formData.organization),
                designation: encode(formData.designation),
                highestQualification: encode(formData.highestQualification),
            }
            delete encoded.hasWhatsApp
            delete encoded.hasInitiation

            api.call('/add-volunteer', {encoded}).then(res=>{
                setLoaderText('Uploading photo...')
                upload('/set-user-photo', `${res.data.id}.jpeg`).then(()=>{
                    setLoaderText(`Submission complete!`)
                }).catch(e=>{
                    setLoaderText(`Could not upload the photo: ${e}`)
                    console.log(`${e}`)
                }).finally(()=>{
                    alert('Your application form is succesfully submitted for review! You will now be taken to login page. Click OK.')
                    resetForm()
                    localStorage.clear(imageKey)
                    window.open('/login', '_self')
                })
            }).catch((e)=>{
                toast.error(`Could not submit the form: ${e}`)
                setLoaderText('')
                console.log(`${e}`)
            })
        }
    }, [cropped])

    const onAbort = () => setSelected(null)

    const instructions = [
        'Please upload a photo in which your face is clearly visible. A passport photo used for documents is a typical example.',
        'Do not upload photos in which you are wearing things that cover your face like helment, face mask, sun glass etc.',
        'This is not a social media display picture. This photo is meant for identification. In future, we may use this photo to print in Volunteer Badge. So please upload a proper photo.'
    ]

    return (
        <div className="reg-main">

            {loaderText && <PageLoader block text={loaderText} black/>}

            {selected && <CropLoader src={selected} minSize={minPhotoSize} onComplete={crop} onAbort={onAbort}/>}

            <div className="reg-header">
                <DP className="reg-logo" url="/favicon.png" size={15}/>
                <div className="reg-head-text">
                    <div className="reg-head-title">SevaBase Application Form</div>
                    <div className="reg-head-desc">ISKCON Mysore Volunteering</div>
                </div>
            </div>

            <div className="reg-dp-main">
                {cropped ? <DP url={cropped} size={40} badge={
                    {
                        size: 10,
                        color: 'var(--bar)',                        
                        icon: {
                            name: 'edit',
                            color: 'white',
                            onClick: select
                        }
                    }
                }/>:<div className="reg-dp-cont">
                        <div className="reg-dp-inst">
                            <div className="reg-inst-head">Instructions (Do not skip, please read completely):</div>
                            <div>
                                {
                                    instructions.map(inst => <div className="reg-in">{`• ${inst}`}</div>)
                                }
                            </div>
                        </div>
                        <div className="reg-dp-circle" onClick={select}>
                            <Icon  name="add-user-photo" size={cropped?[30, 20, 20]:[40, 20, 20]} stroke="#fff" color="none" thickness="1.5" className="reg-dp-plus"/>
                        </div>
                        <div className="reg-dp-desc">Click on the circle to upload your photo</div>
                        <a className="reg-over" href="/login" target="_self">If you have already submitted this form, click here!</a>
                    </div>
                }
            </div>

            {formConfig?
                <div className="reg-form-cont">
                    {cropped && <Form
                        name='registration'
                        config={formConfig}
                        onSubmit={submitForm}
                        user='editor'
                        persist
                        paginate/>}
                </div>:
                cropped && <PageLoader text="Loading data..."/>
            }

        </div>
    )
}

export default Register