import { useCallback, useEffect, useRef, useState } from "react"
import "./index.css"
import Field from "./field"
import { toast } from 'react-toastify'
import { fieldError, getPersistedPage, getPersistedValues, persistData, getPageValidity } from "./util"

const useForm = (name, persist, config) => {
    const formName = `form-${name}`

    const [values, setValues] = useState(getPersistedValues(formName, persist))
    var [ page, setPage ] = useState(getPersistedPage(formName, persist))
    var [ pageValidity, setPageValidity ] = useState()

    const updateValues = (name, value) => {
        const v = { ...values, [name]: value }
        setValues(v)
    }

    useEffect(()=>{
        persistData(formName, values, page)
        setPageValidity(getPageValidity(config[page], values))
    }, [page, values])

    const resetForm = ()=>{
        localStorage.clear(formName)
        window.location.reload()
    }

    const next = useCallback(()=>{
        if(pageValidity){
            setPage(page => page===config.length-1?page:page+1)
        }else{
            toast.error("Some fields are invalid! Please check..")
        }
        return pageValidity
    }, [page, pageValidity])

    const back = useCallback(()=>{
        setPage(page => page==0?config.length-1:page-1)
    }, [page, pageValidity])

    return { values, page, pageValidity, next, back, updateValues, resetForm }
}

const Form = props => {

    const { name, config, onFieldUpdate, onSubmit, paginate, persist, user} = props
    const { values, page, pageValidity, next, back, updateValues, resetForm } = useForm(name, persist, config)

    const handleFieldUpdate = useCallback((name, value) => {
        updateValues(name, value)
    })

    const handleSubmit = useCallback(() => {
        if(pageValidity){
            onSubmit(values, resetForm)
        }else{
            toast.error("Some fields are invalid! Please check..")
        }
    }, [values, pageValidity])

    useEffect(()=>{
        onFieldUpdate && onFieldUpdate(values)
    }, [values])

    const ref = useRef()
    useEffect(()=>{
        page && ref.current.scrollIntoView({ behavior: 'smooth' })
    }, [page])

    const handleResetField = ()=>{
        if(window.confirm('Are you sure you want to reset all the fields?')){
            resetForm()
        }
    }

    return (
        <div className="form-main" ref={ref}>

            <div className="form-head">
                <div className="form-cdef" onClick={handleResetField}>{'Reset Form'}</div>
                <div className="form-mand">{'* indicates mandatory field'}</div>
            </div>

            <div>
                {
                    config.map((c, i) => {

                        return <div className={`form-section ${paginate && page!=i?'hide':''}`} key={`form-section-${i}`}>
                                <div className="form-section-label">{(c.section.user || c.section).toUpperCase()}</div>
                                <hr/>
                                <div className="form-field-group">
                                    {
                                        c.fields.filter(c=>c.condition?c.condition(values):true).map(c=>{
                                            return (
                                                <div className={`form-item`}>
                                                    <Field
                                                        config={c}
                                                        onEditComplete={handleFieldUpdate}
                                                        values={values}
                                                        def={values[c.name]}
                                                        user={user}
                                                    />
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                            </div>
                    })
                }
            </div>

            {paginate && <div className="form-paginator">

                <div className="form-page">{`Page ${page+1} of ${config.length}`}</div>

                <div className="form-indicators">
                    {
                        config.map((c, i)=> <div className={`form-indicator ${i<page?'over':''}`}/>)
                    }
                </div>

                <div className="form-navigators">
                    {page!=0 && <button tabIndex={0} className="form-navigator" onClick={back}>Back</button>}
                    {page!=config.length-1 && <button tabIndex={0} className={`form-navigator`} onClick={next}>Next</button>}
                    {page==config.length-1 && <button tabIndex={0} className={`form-navigator`} onClick={handleSubmit}>Submit</button>}
                </div>
            </div>}
        </div>
    )
}

export default Form