import { useState } from 'react'
import './index.css'
import Input from '../../input'
import { fieldError } from '../util'

const resolveValue = (f, values) => !!f && (typeof(f)==='function'?f(values):f)

const Field = props => {

  const { config, onEditComplete, values, def } = props
  const { name, title, type, min, max, mandatory, desc, post, options, empty } = config

  var mandatoryValue = resolveValue(mandatory, values)
  var titleValue = resolveValue(title, values)
  var descValue = resolveValue(desc, values)

  var [ error, setError ] = useState(def!==undefined && fieldError && fieldError(config, def, values))

  const handleEditComplete = value => {
    const e = fieldError && fieldError(config, value, values)
    onEditComplete && onEditComplete(name, value)
    setError(e)
  }

  // clear error when focused
  const onFocusChange = f => f && setError('')

  return (
    <div className='field-main'>

      {/* Label block */}
      {titleValue && <div className='field-label-holder'>
        <div className="field-label">{titleValue}</div>
        <div className='field-mand'>{mandatoryValue?' *':''}</div>
      </div>}
      
      {/* Description block */}
      {descValue && <div className='field-desc'>{descValue}</div>}

      {/* Input box block */}
      <div className={`field-cont ${error && 'error'} ${type=='checkbox'?'noborder':''}`}>
        <Input
          key={name}
          type={type}
          min={min}
          max={max}
          onEditComplete={handleEditComplete}
          onFocusChange={onFocusChange}
          post={post}
          empty={empty}
          options={options}
          def={def}
        />
      </div>
      
      {/* Warning block */}
      {error && <div className='field-error'>{error}</div>}
    </div>
  )
}

export default Field
