import "./index.css"
import Header from "../../components/header"
import { useGlobal } from "../../context/global"
import { useEffect, useState } from "react"
import { toast } from "react-toastify"
import Applicant from "./applicant"


const Applicants = props => {

    const { api } = useGlobal()
    var [ applicants, setApplicants ] = useState()
    var [ formConfig, setFormConfig ] = useState()

    const getApplicants = () => {
        api.call('/get-applicants').then(res => {
            setApplicants(res.data[0])
        }).catch(e=>{
            console.log('here', e)
        })
    }

    useEffect(getApplicants, [])

    return (
        <div className="aplc-root">

            <Header title={'Applicants'}/>
            <div className="aplc-main">
                
                {applicants!==undefined && <div className="aplc-count">{`${applicants.length || "No"} applicants found`}</div>}
                
                {
                    <div className="aplc-list">
                        {
                            (applicants||[]).map(applicant=>
                                <Applicant applicant={applicant}/>
                            )
                        }
                    </div>
                }
            </div>
        </div>
    )

}

export default Applicants