import "./index.css"
import Swipe from "../../../components/swipe"
import DP from "../../../components/dp"
import Icon from "../../../components/icon"
import { useState } from "react"
import ContactBubble from "../../../components/contactBubble"
import { useNavigate } from "react-router-dom"
import ContextMenu from "../../../components/contextmenu"
import ApplicantBio from "./bio"

const Applicant = ({applicant}) => {

    const navigate = useNavigate()
    var [ showApplicantCall, setShowApplicantCall ] = useState(false)
    var [ showContextMenu, setShowContextMenu ] = useState(false)
    var [ showBio, setShowBio ] = useState(false)

    const onCall = () => {
        setShowApplicantCall(true)
    }

    const closeCall = () => {
        setShowApplicantCall(false)
    }

    const contextMenuConfig = [
        {
            name: 'Pass',
            onClick: ()=>{}
        },
        {
            name: 'Delete',
            onClick: ()=>{},
            className: 'apl-del'
        }
    ]

    return (
        <div className="apl-main">

            {showBio && <ApplicantBio applicant={applicant} close={()=>{setShowBio(false)}}/>}

            {showApplicantCall && <ContactBubble name={applicant.name} phone={applicant.phone} whatsApp={applicant.whatsApp} close={closeCall}/>}

            <Swipe right={{
                maxDrag: 20,
                onSwipe: onCall
            }}>
                <div className="apl-root">
                    <DP user={applicant} size={12.5} enlarge={true}/>
                    <div className="apl-body-holder">
                        <div onClick={()=>{setShowBio(true)}}>
                            <div className="apl-body">
                                <div className="apl-details">
                                    <div className="apl-name">{applicant.initiationName || applicant.name}</div>
                                </div>
                            </div>
                        </div>

                        <div>
                            <div>
                                <Icon name='more-horiz' color='#888' onClick={()=>{setShowContextMenu(true)}}/>
                                {showContextMenu && <ContextMenu close={()=>{setShowContextMenu(false)}} orientation={'left'} config={contextMenuConfig}/>}
                            </div>
                        </div>

                    </div>

                </div>
            </Swipe>
        </div>
    )
}

export default  Applicant