import DP from "../../../../components/dp"
import Glass from "../../../../components/glass"
import Icon from "../../../../components/icon"
import "./index.css"

const ApplicantBio = ({applicant, close}) => {

    console.log(applicant)

    return (
        <div className="apbio-root">
            <Glass dark opacity={0.4}/>
            <div className="apbio-box">

                <div className="apbio-header">
                    <div className="apbio-head">{applicant.name}</div>
                    <Icon name='close' color='white' size={4} onClick={close}/>
                </div>

                <div>
                    <DP user={applicant}/>
                    <div>
                        <div>{applicant.name}</div>
                    </div>
                </div>

            </div>
        </div>
    )
}

export default ApplicantBio