import "./index.css"
import DP from "../../../../components/dp"
import Swipe from "../../../../components/swipe"
import ContactBubble from "../../../../components/contactBubble"
import Icon from "../../../../components/icon"
import { useGlobal } from "../../../../context/global"
import { useEffect, useState } from "react"
import PageLoader from "../../../../components/pageloader"
import moment from "moment"

const VolunteerHome = ()=>{

    const { auth, api } = useGlobal()
    const { user } = auth

    var [ volunteerData, setVolunteerData ] = useState()

    useEffect(()=>{
        api.call('/get-home-data', user).then(res=>{
            setVolunteerData(res.data)
        })
    }, [])

    return (
        volunteerData?
            <div className="volhome-main">
                <Icon name="timer" size="25" color="#888"/>
                <div className="volhome-wait-text">{`Your application form has been submitted. It will be reviewed by the Volunteer Care Cell. This review may take around 2 weeks from the date of submission. You have submitted the form on ${moment(volunteerData.date, "YYYY-MM-DD").format("Do MMM YYYY")}.`}</div>
            </div>
        :<PageLoader text={"Loading home feed..."}/>
    )
}

export default VolunteerHome